export default {
	data() {
		return {
			time01:0,  //  发布时间升序  发布时间降序
			time02:0,  //  截止时间升序  截止时间降序
			seartext:'',   //搜索内容
			pageNum:0, //当前页数
			mydata:[]
		}
	},
	methods: {
		cancel:function(){
			// 取消搜索
			this.seartext=''
			uni.hideKeyboard()
		},
		// 筛选条件
		sort:function(type){
			if(type==0){
				// 综合排序
				this.time01=this.time02=0
			}else if(type==1){
				// 发布时间排序
				this.time01=this.time01==0?1:0
			}else if(type==2){
				// 发布时间排序
				this.time02=this.time02==0?1:0
			}
			this.pageNum=0
			this.mydata=[]
			this.myajax()
		},
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			});
		},
		// 拉取数据
		myajax:function(){
			var that=this
			this.pageNum++
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
				method:'POST',
			    success: (res) => {
					that.mydata.push(res.data)
			    }
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 1000);
		}
	},
	onShow: function(){
		this.pageNum=0
		this.mydata=[]
		this.myajax()
	}
}
